<template>
    <div class="container">
      <el-row>
        <el-col :span="8">
          <h1 class="title">{{pageName}}城市</h1>
          <label for="">城市名称</label><el-input v-model="task.city_name" placeholder="城市名称"></el-input>

          <label for="">城市二字节</label><el-input v-model="task.city_code" placeholder="城市二字节"></el-input>
          <label for="">汇率</label><el-input v-model="task.exchange_rate" placeholder="汇率"></el-input>
          <label for="">是否启用</label>
          <el-select v-model="task.is_del" placeholder="请选择">
            <el-option
              v-for="item in degreeOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>

        </el-col>
      </el-row>
      <div class="footerButton border-shadow">
        <el-button type="primary" @click="submitTask()" style="vertical-align: top">提交</el-button>
      </div>
    </div>
</template>

<script>
    export default {
      name: "addWebsite",
      data() {
          return {
            pageName: '添加',
            task: {
                city_name: "",
                city_code: "",
                exchange_rate: "",
                is_del: "",
            },

            degreeOptions: [
              {
                label: '是',
                value: '0'
              },
              {
                label: '否',
                value: '1'
              },

            ]
          }
      },
      methods: {
        submitTask() {
          let url = this.task.task_id ? "/v1/salescitys/"+this.task.task_id : "/v1/salescitys";
          let params = {};
          let axios = null;
          params.city_code = this.task.city_code;
          params.city_name = this.task.city_name;
          params.exchange_rate = this.task.exchange_rate;
          params.is_del = this.task.is_del;

          let that = this;

          if (this.task.task_id){
               axios= this.$axios.put(url,params)
          }else {
              axios=this.$axios.post(url,params)
          }
            axios.then((res) => {
            if(that.task.task_id){
              that.$message({
                type: 'success',
                message: '更新成功!'
              });
            }else {
              that.$message({
                type: 'success',
                message: '新增成功!'
              });
            }
            that.task.city_code = '';
            that.task.city_name = '';
            that.task.exchange_rate = '';
            that.task.is_del = '';

          }).catch((err) => {
            if(that.task.task_id){
              that.$message({
                type: 'error',
                message: '更新失败!'
              });
            }else {
              that.$message({
                type: 'error',
                message: '新增失败!'
              });
            }
          })
        },
        beforeUpload(file) {
          this.uploadParam.token = this.$md5('salt001'+this.uploadParam.time.toString())
          let fileName = file.name;
          this.uploadParam.format = fileName.slice(fileName.lastIndexOf('.')+1);
          this.uploadParam.file = file;
        },
        uploadSuccess(response, file, fileList) {
          if(response.status==0){
            this.task.content_url = "http://img.orkotech.com/" + response.path
          }else{
            this.$message.error(response.msg);
          }
        },
        uploadFail(err, file, fileList) {
          this.$message.error(err);
        }
      },
      created() {
        let row = this.$route.query;
        if(row.city_name){
          this.pageName = '修改';
          this.task.task_id = row.task_id;
        }
        this.task.city_code = row.city_code
        this.task.city_name = row.city_name
        this.task.exchange_rate = row.exchange_rate
        this.task.is_del = row.is_del

      }
    }
</script>

<style scoped lang="stylus">
  .container
    .el-row
      margin 30px 30px 0px 30px
    label
      display block
      font-weight bold
      font-size: 20px
      margin: 10px 0
    .title
      margin-bottom: 50px
    .button
      margin-top: 20px
    .upload-img
      display inline-block
      margin-right 30px
    .footerButton
      position absolute
      left: 0
      bottom 0
      width: 100%
      padding 13px 30px
      box-sizing border-box
      background-color: hsla(0,0%,100%,.9);
      z-index 2
      button
        float: right;

</style>



